




















































import common from '../../common/common.js';
import {
	mapState
} from 'vuex';

export default {
	data() {
		return {
			lifelist: [],
			lifecat: "",
			title: "",
			pushid: ""
		}
	},
	computed: {
		...mapState(['user']),
	},
	methods: {
		islogin: function() {
			if (this.user.user === null) {
				uni.redirectTo({
					url: '../../pages/manage/login'
				})
				return;
			}
		},
		traindetail: function(lifeid) {
			uni.navigateTo({
				url: "traindetail?lifeid=" + lifeid
			})
		},
		getlist: function(e) {
			let that = this;
			//console.log(this.user.user.token);
			uni.request({
				url: common.hosturl + "life/getlifemessage.html",
				method: "POST",
				data: {
					token: this.user.user.token,
					pushid: this.pushid
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
					console.log(JSON.stringify(res.data));
					if (res.data.code == 0) {
						that.lifelist = res.data.list;

					} else {
						uni.showToast({
							icon: 'none',
							title: '出错啦，请联系客服'
						});
					}
				},
				fail: function() {
					uni.showToast({
						icon: 'none',
						title: '网络错误'
					});
				}
			})
		},



	},
	onLoad: function(e) {
		this.pushid = e.pushid;
		this.islogin();

	},
	onShow: function(e) {
		this.getlist();

	}
}



